<template>
  <span style="color: red">
    <i>{{display | currency(vndConfig)}}</i>
  </span>
</template>
<script>
export default {
  name: "vndPrice",
  props: ["value"],
  mounted: function() {},
  computed: {
    display: function() {
      return parseFloat(this.value) * parseFloat(this.appSettings.vndExchangeRate);
    }
  }
};
</script>